import { getRepoContent } from '@/apis/repo';
import { useUserStore } from '@/store';
import { toReject, toResolve } from '@/utils';
import { defineStore } from 'pinia';
import { usePhotoAlbumAsideStore } from '../PhotoAlbumAside/store';
import { usePhotoAlbumPagerStore } from '../PhotoAlbumPager/store';

export { usePhotoAlbumViewStore, IPhotoAlbumViewState };

interface IPhotoAlbumViewState {
    repoContentList: {
        src: string;
        index: number;
        sha: string;
    }[];
    curRepoContentPageList: {
        src: string;
        index: number;
        sha: string;
    }[];
}

const usePhotoAlbumViewStore = defineStore('photoAlbumView', {
    state(): IPhotoAlbumViewState {
        return {
            repoContentList: [],
            curRepoContentPageList: [],
        };
    },
    getters: {
        username() {
            return useUserStore().username;
        },
        curPicRepo() {
            return usePhotoAlbumAsideStore().curPicRepo;
        },
        pageSize() {
            return usePhotoAlbumPagerStore().pageSize;
        },
        curPage() {
            return usePhotoAlbumPagerStore().curPage;
        },
    },
    persist: {
        enabled: true,
        strategies: [
            {
                storage: window.localStorage,
            },
        ],
    },
    actions: {
        updateRepoContentList: async function () {
            const [error, res] = await getRepoContent(
                this.username,
                this.curPicRepo.name,
            );

            if (error) return toReject(error);

            this.repoContentList = reactive(
                res.map((repo, index) => ({
                    src: repo.download_url,
                    index: index + 1,
                    sha: repo.sha,
                })),
            );

            this.updateCurRepoContentPageList();

            return toResolve(true);
        },

        updateCurRepoContentPageList() {
            this.curRepoContentPageList = this.repoContentList.filter(
                (repo) =>
                    repo.index >=
                        this.curPage * this.pageSize - this.pageSize + 1 &&
                    repo.index <= this.curPage * this.pageSize,
            );
        },
    },
});
