<script setup lang="ts">
import { storeToRefs } from 'pinia';
import { usePhotoAlbumViewStore } from '../PhotoAlbumView/store';
import { usePhotoAlbumPagerStore } from './store';
const pagerStore = usePhotoAlbumPagerStore();
const viewStore = usePhotoAlbumViewStore();
const { curPage, pageSize, pageSizes } = storeToRefs(pagerStore);
const { repoContentList } = storeToRefs(usePhotoAlbumViewStore());

function handleSizeChange(pageSize: number) {
    pagerStore.setPageSize(pageSize);
    viewStore.updateCurRepoContentPageList();
}
function handleCurrentChange(curPage: number) {
    console.log('curPage::', curPage);
    pagerStore.setCurPage(curPage);
    viewStore.updateCurRepoContentPageList();
}
</script>
<template>
    <el-pagination
        v-model:currentPage="curPage"
        style="justify-content: center; margin-top: 10px"
        :page-size="pageSize"
        :page-sizes="pageSizes"
        small
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="repoContentList.length"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
    />
</template>

<style scoped></style>
