import { defineStore } from 'pinia';
export { usePhotoAlbumPagerStore };
const usePhotoAlbumPagerStore = defineStore('photoAlbumPager', {
    state() {
        return {
            curPage: 1,
            pageSize: 30,
            pageSizes: [30, 60, 100],
        };
    },
    getters: {},
    persist: {
        enabled: true,
        strategies: [
            {
                storage: window.localStorage,
            },
        ],
    },
    actions: {
        setCurPage(page: number) {
            this.curPage = page;
        },
        setPageSize(pageSize: number) {
            this.pageSize = pageSize;
        },
    },
});
