<script setup lang="ts">
import { ILsRepoForAuthUserRes } from '@/apis/repo';
import { useLoading } from '@/hooks';
import { ElMessage } from 'element-plus';
import { storeToRefs } from 'pinia';
import { usePhotoAlbumViewStore } from '../PhotoAlbumView/store';
import { usePhotoAlbumAsideStore } from './store';
const asideStore = usePhotoAlbumAsideStore();
const { picRepoList, isShow } = storeToRefs(asideStore);

// init
asideStore.updatePicRepoList();

async function handleNodeClick(repo: ILsRepoForAuthUserRes) {
    const loading = useLoading();
    asideStore.setCurPicRepo(repo);
    const [error] = await usePhotoAlbumViewStore().updateRepoContentList();
    if (error) return error;

    ElMessage.success('');
    loading.close();
}
</script>
<template>
    <el-aside v-show="isShow" width="auto">
        <el-tree
            :data="picRepoList"
            :props="{ label: 'name' }"
            @node-click="handleNodeClick"
        />
    </el-aside>
</template>

<style>
.el-aside {
    margin-right: 10px;
}
</style>
